
import { defineComponent, ref, reactive } from "vue";
import { StaffInfo } from "@/shared/type/COM20300211.type";
import { COM20300221_REQ_BODY, COM20300221_RES } from "@/shared/type/COM20300221.type";
import { COM20300231_REQ_BODY, COM20300231_RES } from "@/shared/type/COM20300231.type";
import { COM20300212_REQ_BODY, COM20300212_RES } from "@/shared/type/COM20300212.type"
import { COM20300241_REQ_BODY, COM20300241_RES } from "@/shared/type/COM20300241.type"
import { BranchDeptInfo, COM20200111_REQ_BODY, COM20200111_RES } from '@/shared/type/COM20200111.type';
import VueDatePicker from '@vuepic/vue-datepicker';
import { modalController } from "@ionic/vue";
import PhoneInput from '@/shared/components/PhoneInput/PhoneInput.vue'
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "com203002sc01",
  components: {
    VueDatePicker,
    PhoneInput,
    ModifyCard,
    BreadCrumb,
    SelectOption
  },
  data() {
    const branchDeptListTable = ref<BranchDeptInfo[]>([])
    const viewType = this.$route.query.type;
    const selectBranchCode = "";
    const input = reactive({
      userTypeCode: "",
      employeeNo: "",
      workStatusCode: "",
      userFirstName: "",
      userLastName: "",
      branchCode: "",
      branchName: "",
      emailAddress: "",
      userStatusCode: "",
      cellPhoneNo: "",
      firstRegisterBranchCode: "",
      firstRegisterTellerID: "",
      firstRegisterTellerName: "",
      firstRegisterDate: "",
      firstRegisterTime: "",
      lastChangeBranchCode: "",
      lastChangeTellerID: "",
      lastChangeTellerName: "",
      lastChangeDate: "",
      lastChangeTime: "",
      jobTitleCode: "",
    });
    const tempObject = ref<StaffInfo>()
    const modifyCard = {
      registerTellerId: '',
      registerTellerName: '',
      registerDate: '',
      registerTime: '',
      updatedTellerId: '',
      updatedTellerName: '',
      updatedDate: '',
      updatedTime: '',
    }
    return {
      isWrongPhoneNumberFormat: false,
      isWrongEmailFormat: false,
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      isRegister: true,
      modifyCard,
      branchDeptListTable,
      selectBranchCode,
      employeeNo: this.$route.query.employeeNo,
      type: this.$route.query.type,
      viewType,
      input,
      isDisabled: ref(false),
      isEdited: ref(false),
      tempObject,
      isDisabledField: ref(true),
      disabled: ref(true),
      isDisabledCode: ref(false),
      isShowErrorText: ref(false),
      isInvalidInput: ref(false),
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'com203002sc00.portal_management', keyPath: '', type: 'label' },
        { value: 'com203002sc00.bank_staff_info', keyPath: '', type: 'label' },
        { value: 'com203002sc00.bank_staff_list', keyPath: 'COM203002SC00', type: 'route' },
        { value: `${this.$route.query.type == 'register' ? 'com203002sc00.bank_staff_register' : 'com203002sc00.bank_staff_edit'}`, keyPath: '/COM203002SCO1', type: 'route' },
      ]
    };
  },
  watch: {
    'input.emailAddress': function (emailValue) {
      if (this.viewType == 'edit') {
        if (emailValue) {
          this.isWrongEmailFormat = false;
        }
      } else if (this.viewType == 'register') {
        if (emailValue) {
          this.isWrongEmailFormat = false;
        }
      }
    },
    'input.cellPhoneNo': function (newValue) {
      if (newValue.length == 0) {
        this.isWrongPhoneNumberFormat = false
      } else if (newValue.length == 9 || newValue.length > 9) {
        this.isWrongPhoneNumberFormat = false
      }
      else if (newValue.length < 9) {
        this.isWrongPhoneNumberFormat = true
      }
    }
  },
    computed: {
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
    workStatusCode() {
      return this.$store.state.CommonCodeModule.commonCode.WorkStatusCode
      },
      userStatusCode() {
      return this.$store.state.CommonCodeModule.commonCode.UserStatusCodes
    }
    },
    async mounted() {
    if (this.approvalType === '') {
      if (this.$route.query.type == "edit") {
        await this.inquiryBranchDepList()
        await this.handleFetchStaffDetail();
        this.isRegister = false;
        this.isDisabledCode = true
        this.isDisabled = true;
        this.isEdited = true
      } else {
        this.input.workStatusCode = this.workStatusCode[0].key
        this.input.userStatusCode = this.userStatusCode[0].key
        this.inquiryBranchDepList()
        this.isRegister = true
        this.isDisabledCode = true
        this.isDisabledField = false
        this.isDisabled = false;
        this.isEdited = false
      }
    } else {
      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
        await this.inquiryBranchDepList()
        this.selectBranchCode = authDetailParsed.branchCode
        this.input.branchName = authDetailParsed.branchName
        this.input.userTypeCode = authDetailParsed.userTypeCode
        this.input.employeeNo = authDetailParsed.employeeNo
        this.input.workStatusCode = authDetailParsed.workStatusCode
        this.input.userFirstName = authDetailParsed.userFirstName
        this.input.userLastName = authDetailParsed.userLastName
        this.input.emailAddress = authDetailParsed.emailAddress
        this.input.userStatusCode = authDetailParsed.userStatusCode
        this.input.cellPhoneNo = authDetailParsed.cellPhoneNo
      })
    }
  },
  methods: {
    // To Select Branch Code 
    async inquiryBranchDepList() {
      const _sTrcode = "COM20200113";
      const _bMock = false;
      const _oBody: COM20200111_REQ_BODY = {
        branchCode: this.selectBranchCode,
        branchName: this.selectBranchCode,
        branchStatusCode: "",
        branchTypeCode: ""
      };

      const res = await this.$request.getRequest(_sTrcode, _oBody, _bMock) as COM20200111_RES;
      
      if (res.header.result) {
        this.branchDeptListTable = JSON.parse(JSON.stringify(res.body.list));
        if (this.$route.query.type == "register") {
          // this.selectBranchCode = await res.body.list[0].branchCode;
        } else {
          this.selectBranchCode = this.input.branchCode
        }
      }
    },

    handleClickAction(actionType: string) {
      switch (actionType) {
        case "save":
          if (this.viewType == "edit") {
            this.isDisabled = true;
            this.isEdited = true
            this.isDisabledCode = true
            this.handleUpdateEmployeeData();
          } else {
            this.isDisabled = true;
            this.isEdited = false
            this.handleRegisterEmployeeData();
          }
          break;
        case "cancel":
          if (this.viewType == "edit") {
            this.handleFetchStaffDetail()
            this.isDisabled = true;
            this.isEdited = true
            this.isDisabledCode = true
          } else {
            this.$router.push('/COM203002SC00')
          }
          break;
        default:
      }
    },
    handleEditButton() {
      if (this.$route.query.type == "edit") {
        this.isDisabled = false;
        this.isEdited = false;
        this.isDisabledCode = false;
      } else {
        this.isDisabled = true;
        this.isEdited = true
      }
    },
    handleSaveButton() {
      let wrongEmailFormat = false;
      // let cellPhoneNo = false;
      this.isInvalidInput = false;
        wrongEmailFormat = this.$validate.validateEmail(this.input.emailAddress);
      if ( wrongEmailFormat && 
          this.input.cellPhoneNo.length >= 9 && 
          this.input.employeeNo && 
          this.input.userTypeCode && 
          this.input.workStatusCode && 
          this.input.userFirstName && 
          this.input.userLastName && 
          this.input.userStatusCode && 
          this.input.jobTitleCode && 
          this.selectBranchCode ) {
        this.$dialog.onOpen({
            content: this.$t("message.would_you_like_to_save"),
            dialogMode: "Question",
          callback: () => {
            if (this.viewType == "edit") {
                this.isDisabled = true;
                this.isEdited = true;
                this.isDisabledCode = true;
                this.handleUpdateEmployeeData();
            } else {
                this.isDisabled = true;
                this.isEdited = false
                this.handleRegisterEmployeeData();
              }
            }
          });
        } else {
          this.isInvalidInput = true;  //check the required field 
        wrongEmailFormat ? this.isWrongEmailFormat = false : this.isWrongEmailFormat = true;
      }
    },

    handleListButton() {
      this.$router.push('/COM203002SC00')
    },
    // Delete
    handleDeleteButton() {
      this.$dialog.onOpen({
        content: this.$t("message.are_you_sure_you_want_to_delete"),
        dialogMode: 'Question',
        callback: () => {
          modalController.dismiss()
          this.EmployeeInfoDelete();
        }
      });
    },
    handleCancelButton() {
      this.$dialog.onOpen({
        content: this.viewType === 'edit' ? this.$t('message.cancel_without_saving_changes') : this.$t('message.are_you_sure_you_want_to_cancel_registration?'),
        dialogMode: 'Question',
        callback: () => {
          this.handleClickAction('cancel')
          modalController.dismiss()
        }
      });
    },
    // Delete (COM20300241)
    async EmployeeInfoDelete() {
      const trCode = "COM20300241"
      const bMock = false
      const body: COM20300241_REQ_BODY = {
        employeeNo: this.input.employeeNo,
      }
      const resData: any = (await this.$request.getRequest(trCode, body, bMock)) as COM20300241_RES
      if (resData.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        });
      }
    },
    // FetchDetail (COM20300212)
    async handleFetchStaffDetail() {
      const body: COM20300212_REQ_BODY = {
        employeeNo: `${this.$route.query.employeeNo}`,        //Employee No
      };
      const res = (await this.$request.getRequest("COM20300212", body, false)) as COM20300212_RES
      if (res.header.result) {
        this.input = JSON.parse(JSON.stringify(res.body));

        this.selectBranchCode = this.input.branchCode;
        this.modifyCard = {
          registerTellerId: res?.body.firstRegisterTellerID,
          registerTellerName: res?.body.firstRegisterTellerName,
          registerDate: res?.body.firstRegisterDate,
          registerTime: res?.body.firstRegisterTime,
          updatedTellerId: res?.body.lastChangeTellerID,
          updatedTellerName: res?.body.lastChangeTellerName,
          updatedDate: res?.body.lastChangeDate,
          updatedTime: res?.body.lastChangeTime,
        }
      }
    },
    // Employee Update (COM20300231)
    async handleUpdateEmployeeData() {
      const trCode = "COM20300231"
      const bMock = false
      const body: COM20300231_REQ_BODY = {
        branchCode: this.selectBranchCode, //Branch Code
        cellPhoneNo: this.input.cellPhoneNo, //Cell Phone No
        emailAddress: this.input.emailAddress, //Email Address
        employeeNo: this.input.employeeNo, //Employee No
        userFirstName: this.input.userFirstName, //User First Name
        userLastName: this.input.userLastName, //User Last Name
        userStatusCode: this.input.userStatusCode, //User Status Code
        userTypeCode: this.input.userTypeCode, //User Type Code
        workStatusCode: this.input.workStatusCode, //Work Status Code
        jobTitleCode: this.input.jobTitleCode, //Work Status Code
      };
      const res = await this.$request.getRequest(trCode, body, bMock) as COM20300231_RES
      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss();
            this.handleFetchStaffDetail();
          }
        });
      }
    },
    // Register Employee (COM20300221)
    async handleRegisterEmployeeData() {
      const trCode = "COM20300221"
      const bMock = false
      const body: COM20300221_REQ_BODY = {
        employeeNo: this.input.employeeNo,
        branchCode: this.selectBranchCode,
        cellPhoneNo: this.input.cellPhoneNo,
        emailAddress: this.input.emailAddress,
        userFirstName: this.input.userFirstName,
        userLastName: this.input.userLastName,
        userStatusCode: this.input.userStatusCode,
        userTypeCode: this.input.userTypeCode,
        workStatusCode: this.input.workStatusCode,
        jobTitleCode: this.input.jobTitleCode
      };
      const res = await this.$request.getRequest(trCode, body, bMock) as COM20300221_RES
      if (res.header.result) {
        this.$router.push('/COM203002SC00')
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        });
      }
    },
    validateEmail(email: string) {
      const regEx = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)

      return regEx.test(email)
    }
  },
});
